<template>
  <div>
    <div class="grid brd">
      <h1>{{grid_name}}</h1>
      <div class="grid__header">
        <div class="grid__header_title" v-for="column in title_list">
          {{column}}
        </div>
      </div>
      <div v-for="(item, idx) in data_list">
        <grid-item
          @remove="onRemoveEvent(idx)"
          @change="onChangeEvent(idx, $event)"
          :object_name="item.object_name"
          :object_cost="item.object_cost"
          :object_count="item.object_count"
        ></grid-item>
      </div>
      <div v-if="addItem">
        <grid-item
          @remove="onRemoveNewItemEvent"
          @change="onSaveNewItemEvent"
          :editing="true"></grid-item>
      </div>
      <footer>
        Итог: {{ total }}
      </footer>
    </div>
    <button class="add_btn" @click="onAddClick">Добавить</button>
  </div>
</template>

<script>
  import GridItem from './GridItem.vue'
  export default {
    name: 'Grid',
    components: {
      GridItem
    },
    props: {
      grid_name: {
        type: String
      },
      title_list: {
        type: Array
      },
      data_list: {
        type: Array
      }
    },
    data () {
      return {
        addItem: false,
      }
    },
    computed: {
      total() {
        return this.data_list.reduce((total, item) => {
          return total + item.object_cost * item.object_count;
        }, 0);
      }
    },
    methods: {
      onRemoveEvent(idx) {
        this.$emit('removeElement', idx);
      },
      onChangeEvent(idx, event) {
        this.$emit('changeElement', event, idx);
      },
      onAddClick() {
        this.addItem = !this.addItem;
      },
      onRemoveNewItemEvent() {
        this.addItem = !this.addItem;
      },
      onSaveNewItemEvent(event) {
        this.$emit('addElement', event);
        this.addItem = !this.addItem;
      },
    }
  }
</script>


<style scoped>
  .grid {

  }

  .grid__header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 200px;
    background-color: #31B583;
    padding: 10px 0;
    color: white;
    font-size: 15pt;
  }
  .grid__header_title{
    text-align: left;
    margin: 5px 10px;
  }
  .add_btn {
    align-self: flex-end;
    width: 120px;
    height: 50px;
  }
  h1 {
    text-align: left;
  }
  .brd {
    /*border: 4px double black;*/
  }
  footer {
    background-color: #31B583;
    text-align: left;
    margin: 0 0;
    padding: 10px 10px;
    font-size: 30px;
    color:white;
    flex: 0 0 auto;
  }
</style>



