<template>
  <div class="hello">
    <grid class="grid"
          :grid_name="'Накладная таблица'"
          :data_list="object_list"
          :title_list="title_list"
          @removeElement="onRemoveElement"
          @changeElement="onChangeElement"
          @addElement="onAddElement">

    </grid>
  </div>
</template>

<script>
  import Grid from './shared/Grid.vue'
  export default {
    name: 'main',
    components: {
      Grid
    },
    data() {
      return {
        title_list: ['Наименовение', 'Количество', 'Цена'],
        object_list: [],
      }
    },
    mounted() {
      this.object_list.push({object_name: "bla4", object_count: 4, object_cost: 4});
      this.object_list.push({object_name: "bl5", object_count: 5, object_cost: 5});
      this.object_list.push({object_name: "bla6", object_count: 6, object_cost: 6});

    },
    methods: {
      onRemoveElement(idx) {
        this.object_list.splice(idx, 1);
      },
      onChangeElement(item, idx) {
        //создание нового элемента для хранение его в списке объектов
        const clone = {};
        for (let key in item) {
            clone[key] = item[key];
        }
        this.object_list.splice(idx, 1, clone);
      },
      onAddElement(item) {
        this.object_list.push(item);
      }

    },
  }
</script>

<style scoped>
  .grid {
    margin: 0 auto;
    max-width: 80%;
  }


</style>



