<template>
  <div class="grid_item">
    <div v-if="!edit"  class="cell white">
      {{item.object_name}}
    </div>
    <input v-else  type="text" class="cell" v-model="item.object_name"/>
    <div v-if="!edit"  class="cell gray">
      {{item.object_count}}
    </div>
    <input v-else  type="number" class="cell" v-model.number="item.object_count" />
    <div v-if="!edit"  class="cell white" >
      {{item.object_cost}}
    </div>
    <input v-else  type="number" class="cell" v-model.number="item.object_cost" />
    <div class="gray">
      <button v-if="!edit" @click="edit = true">Изменить</button>
      <button v-else @click="onSaveClick">Сохранить</button>
      <button @click="$emit('remove')">Удалить</button>
    </div>
    </div>
</template>

<script>
  export default {
    name: 'GridItem',
    data () {
      return {
        item: {
          object_name: "",
          count: 0,
          cost:0
        },
        edit: false
      }
    },
    props: {
      object_name: {
        type: String,
        default: ''
      },
        object_count: {
          type: Number,
          default: 0
        },
        object_cost: {
          type: Number,
          default: 0
      },
      editing: {
        type: Boolean,
        default: false
      }
    },
    created() {
      this.edit = this.editing;
      this.item.object_name = this.object_name;
      this.item.object_cost = this.object_cost;
      this.item.object_count = this.object_count;
    },
    methods: {
        onSaveClick() {
            this.$emit('change', this.item);
            this.edit = false;
        }
    }
  }
</script>


<style scoped>
  .grid_item {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 200px;
    grid-column-start: end;
  }
  .cell {
    font-size: 20px;
    text-align: left;
    padding: 10px
  }
  .gray {
    background-color: #F5F5F5;
  }
  .white {
    background-color: white;
  }
  input {
    width: 95%;
    margin: 0 0;
    padding: 0 0;
  }
  button {
    width: 48.5%
  }
</style>



